.selectedpage {
    border: 1px solid #878f9c;
    border-radius: 20px;
    width: 40px;
    height: 40px;
    background-color: white;
    color: black;
    font-weight: bold;
    cursor: pointer;
}
.page {
    border: 1px solid #878f9c;
    border-radius: 20px;
    width: 40px;
    height: 40px;
    background-color: white;
    color: #878f9c;
    cursor: pointer;
}
.page:hover {
    color: black;
}
.block {
    width: 100%;
    display: flex;
    max-width: 900px;
    justify-content: space-around;
    margin-top: 10px;
    margin-bottom: 30px;
}
.navbtn {
    margin-top: -15px;
    font-size: 40px;
    border: none;
    background-color: white;
    outline: none;
    cursor: pointer;
}
.navbtnprev {
    transform: rotate(180deg);
    margin-top: -10px;
    font-size: 40px;
    border: none;
    background-color: white;
    outline: none;
    cursor: pointer;
}
