.block {
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 4.375rem;
    margin-right: 1rem;
}
.main {
    margin-left: 13px;
    border-bottom: 1px solid #d6d7da;
}
.img {
    width: 3rem;
    height: 3rem;
}
.list {
    display: flex;
    list-style: none;
    margin: 0px 0px 0px 0px;
}
.profile {
    width: 3rem;
    height: 3rem;
}
.search {
    cursor: pointer;
    margin-bottom: 10px;
    margin-right: 13px;
    width: 1.25rem;
    height: 1.25rem;
    align-self: center;
}
.link {
    text-decoration: none;
    font-size: 13px;
    line-height: 16px;
    margin-right: 2.5rem;
    color: #788292;
    opacity: 0.7;
}
.loginblock {
    display: flex;
    flex-direction: column;
    margin-right: 10px;
}
.name {
    margin-bottom: 5px;
}
.logout {
    height: 30px;
    border-radius: 5px;
    background: #f0f7ff;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
    color: #1877f2;
    cursor: pointer;
    border: none;
    outline: none;
    margin-right: 5px;
}
.spanlogin {
    text-decoration: none;
    color: black;
}
.logout:hover {
    transition: all 0.3s;
    color: #fcfcfc;
    background: #0e58ac;
}
